export function apiPost(api, data = {}) {
    return ajax({
        url: api,
        method: 'POST',
        data,
    });
}

export function apiGet(api, params = {}) {
    return ajax({
        url: api,
        method: 'GET',
        params,
    });
}

export async function asyncFn(api, data = {}) {
    try {
        let res = await apiPost(api, data);
        return res.data;
    } catch (error) {
        console.log(error);
    }
}

