export const fmtAuth = (orignAuth, enableAuth = null) => {
    let enableIds = enableAuth ? enableAuth.map(i => i.permission_id) : [];
    let rootSelecte = enableIds.indexOf(orignAuth.node.permission_id) === -1;
    let filterData = [];
    let treeData = [];
    // console.log(enableIds,rootSelecte);
    filterLoop(orignAuth.childNodes);
    treeLoop(treeData, 0, rootSelecte);
    // 第一步
    function filterLoop(arr) {
        arr.forEach(item => {
            filterData.push({
                id: item.node.permission_id,
                name: item.node.permission_name,
                parent: item.node.parent_id,
            });
            if (item.childNodes && item.childNodes.length > 0) {
                filterLoop(item.childNodes);
            }
        });
    }

    function isAble(pSelecte, item) {
        if (item.id === 26) return true;
        if (enableIds.length === 0) return false;
        // false 可选 true 不可选
        if (pSelecte === false) return false;
        if (enableIds.indexOf(item.id) !== -1) return false;
        return true;
    }

    function treeLoop(arr, pid, pSelecte) {
        // 父级的孩子组，父级ID, 父级可选
        filterData.forEach(item => {
            if (item.parent === pid) {
                arr.push({
                    disabled: isAble(pSelecte, item),
                    title: item.name,
                    key: item.id,
                    value: item.id,
                    children: [],
                });
                treeLoop(
                    arr[arr.length - 1].children,
                    item.id,
                    arr[arr.length - 1].disabled,
                );
            }
        });
    }
    return [
        {
            disabled: enableIds.length === 0 ? false : rootSelecte,
            title: orignAuth.node.permission_name,
            key: orignAuth.node.permission_id,
            value: orignAuth.node.permission_id,
            children: treeData,
        },
    ];
};

export const fmtArch = (orignData, deepLimit) => {
    let tree = [];
    let loop = (arr, pid, deep) => {
        deep = deep || 1;
        orignData.forEach(item => {
            if (item.org_parent_id === pid && deep <= deepLimit) {
                arr.push({
                    deep, // 深度
                    isEdit: deep !== 1,
                    isAddChild: deep < deepLimit, // 能否添加子节点，1234可添加，5不可以
                    isRemove:
                        deep !== 1 &&
                        typeof orignData.find(
                            i => i.org_parent_id === item.org_id,
                        ) === 'undefined', // 能否删除当前节点，根节点和父节点不可删除
                    title: item.org_name, // 名称
                    key: item.org_id, // 唯一
                    pid: pid,
                    value: item.org_name + '-' + item.org_id, // 搜索
                    scopedSlots: {
                        title: 'title',
                    }, // 自定义
                    children: [],
                });
                loop(
                    arr[arr.length - 1].children,
                    item.org_id,
                    ++arr[arr.length - 1].deep,
                );
            }
        });
    };
    loop(tree, 0);
    return tree;
};
